<template lang="pug">
	div.card-box
		.card.abm(v-bind:class="{disable: !cardStatus}")
			CardHeader(:status="cardStatus", title="Account Based Marketing (ABM)", v-model="cardStatus")

			.abm-top
				.goal
					span Goal
					p 50%
						b.orange ({{ statistic.goal }})

				.done
					span Done
					p 82%
						b.green ({{ statistic.done }})

			.abm-table
				.t-head
					.cl
						p List

					.cl
						p Accounts

					.cl
						p Leads

					.cl
						.chart-range(ref="chart")
							.single
								p 0

							.single
								p {{ maxNumber / maxDivide * 1 }}

							.single
								p {{ maxNumber / maxDivide * 2 }}

							.single
								p {{ maxNumber / maxDivide * 3 }}

							.single
								p {{ maxNumber / maxDivide * 4 }}

							.single
								p {{ maxNumber / maxDivide * 5 }}

				.t-row(v-for="(item, index) in abmList", v-bind:class="{'active': item.status == 'play'}")
					.cl
						.action-play-pause(
							v-if="item.status",
							v-bind:class="{played: item.status == 'play'}"
							v-on:click="changeABMStatus(index)"
						)
							.play
								svg(width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg")
									rect(x="0.5" y="0.5" width="19" height="19" rx="2.5").stroke
									path(d="M7.23822 5.057C7.30311 5.019 7.37378 5 7.44444 5C7.53333 5 7.62133 5.03 7.69733 5.089L13.4751 9.5891C13.5951 9.6826 13.6667 9.83611 13.6667 10.0001C13.6667 10.1641 13.5951 10.3176 13.4751 10.4111L7.69733 14.9112C7.56178 15.0167 7.38444 15.0292 7.23822 14.9432C7.09156 14.8567 7 14.6862 7 14.5002V5.50001C7 5.31401 7.09156 5.1435 7.23822 5.057Z").fill
							.pause
								svg(width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg")
									rect(width="20" height="20" rx="3").f-f
									rect(x="7" y="5" width="2" height="10" rx="1").f-t
									rect(x="11" y="5" width="2" height="10" rx="1").f-t

						.in-row
							p {{ item.name }}
							.file-link

					.cl
						p {{ Number(item.accounts).toLocaleString('en-US') }}

					.cl
						p {{ item.leads }}

					.cl
						.fill-line
							.lines
								.s-line
								.s-line
								.s-line
								.s-line
								.s-line
								.s-line

							.fill(:style='{width: item.leads / maxNumber * 100 + "%"}')
								p(
									v-bind:class="{'push': chartWidth * Number(item.leads / maxNumber * 100).toFixed(2) / 100 < 40 }"
								) {{ Number(item.leads / statistic.goal * 100).toFixed(2) }}% 

				.t-row(v-for="(item, index) in newABMList", v-bind:class="{'active': item.status == 'play'}")
					.cl
						.action-play-pause(
							v-if="item.status",
							v-bind:class="{played: item.status == 'play'}"
							v-on:click="changeNABMStatus(index)"
						)
							.play
								svg(width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg")
									rect(x="0.5" y="0.5" width="19" height="19" rx="2.5").stroke
									path(d="M7.23822 5.057C7.30311 5.019 7.37378 5 7.44444 5C7.53333 5 7.62133 5.03 7.69733 5.089L13.4751 9.5891C13.5951 9.6826 13.6667 9.83611 13.6667 10.0001C13.6667 10.1641 13.5951 10.3176 13.4751 10.4111L7.69733 14.9112C7.56178 15.0167 7.38444 15.0292 7.23822 14.9432C7.09156 14.8567 7 14.6862 7 14.5002V5.50001C7 5.31401 7.09156 5.1435 7.23822 5.057Z").fill
							.pause
								svg(width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg")
									rect(width="20" height="20" rx="3").f-f
									rect(x="7" y="5" width="2" height="10" rx="1").f-t
									rect(x="11" y="5" width="2" height="10" rx="1").f-t

						.in-row
							p look-a-like

					.cl
						p {{ Number(item.accounts).toLocaleString('en-US') }}

					.cl
						p {{ item.leads }}

					.cl
						.fill-line
							.lines
								.s-line
								.s-line
								.s-line
								.s-line
								.s-line
								.s-line

							.fill-orange(:style='{width: item.leads / maxNumber * 100 + "%"}')

			button.btn.add-new-btn(type="button", v-on:click="addList()") ADD A NEW LIST

		.popup.add-asset-p(v-if="popupAddList")
			.close(v-on:click="showHidePopups()")
			.inside-form
				label Name
				input(type="text", placeholder="Text here...", v-model="popupAccounts").inp

				.drop-zone(
					@drop="drop",
					@dragover="dragover",
					@dragleave="dragleave"
				)
					input(
						type="file",
						v-on:change="onChange",
						ref="file").file-picker
					div(v-if="listFile.length > 0")
						p {{ listFile[0].name }}
					div(v-else)
						p BROWSE FILE
						span or
							br
							| Drag and Drop file here

				button.btn.simple(type="button", v-on:click="saveList()") SUBMIT

</template>

<script>
	import CardHeader from "../components/title-checkbox.vue";
	import DropList from "../components/drop-list.vue";

	export default {
		name: 'ABM',
		components:{
			CardHeader,
			DropList
		},
		data() {
			return {
				cardStatus: true,
				statistic:{
					goal: 360,
					done: 296
				},
				abmList: [
					{
						id: 0,
						name: 'file 1',
						list: 'url',
						status: 'play',
						accounts: 1254,
						leads: 296,
					},
					{
						id: 1,
						name: 'file 2',
						list: 'url',
						status: 'play',
						accounts: 12,
						leads: 10,
					}
				],
				newABMList:[
					{
						id: 0,
						name: '',
						list: '',
						status: 'play',
						accounts: '',
						leads: 300,
					}
				],
				maxDivide: 5,
				maxNumber: 100,
				chartWidth: 0,
				popupAddList: false,
				listFile: [],
				popupFileName: '',
				popupAccounts: ''
			}
		},
		created(){
			this.setMaxInt();
		},
		mounted() {
			// window.addEventListener('resize', this.handleResize);
			this.handleResize();
		},
		methods: {
			handleResize() {
				this.chartWidth = this.$refs.chart.clientWidth;
			},
			setMaxInt(){
				var max = this.maxNumber;
				for (var item in this.abmList) {
					var num = parseInt(this.abmList[item].leads);
					if (max < num && !isNaN(num)) {
						max = num
					}
				}

				var r = parseInt(max.toString()[0]);
				check();
				var x;
				function check(){
					x = r * 100;
					if(x < max){
						r = r + 1;
						check();
					}
				}
				this.maxNumber = x;
			},
			changeABMStatus(index){
				if (this.cardStatus) {
					if(this.abmList.[index].status == 'play'){
						this.abmList.[index].status = 'pause'
					}else{
						this.abmList.[index].status = 'play'
					}
				}
			},
			changeNABMStatus(index){
				if (this.cardStatus) {
					if(this.newABMList.[index].status == 'play'){
						this.newABMList.[index].status = 'pause'
					}else{
						this.newABMList.[index].status = 'play'
					}
				}
			},
			showHidePopups(){
				this.listFile = [];
				this.popupAddList = false;
				this.$emit('input', false);
			},
			onChange() {
				this.listFile = [...this.$refs.file.files];
				this.popupAccounts = this.listFile[0].name.replace(/[^0-9]/g,'');
				this.popupFileName = this.listFile[0].name.split('.').slice(0, -1).join('.');
			},
			drop(event) {
				event.preventDefault();
				this.$refs.file.files = event.dataTransfer.files;
				this.onChange();
			},
			dragover() {
				this.fileDropHover = true;
			},
			dragleave(){
				this.fileDropHover = false;
			},
			addList(){
				if (this.cardStatus) {
					this.popupAddList = !this.popupAddList;
					this.$emit('input', this.popupAddList);
				}
			},
			saveList(){
				var key =  Object.keys(this.abmList).length;
				var pattern = {
					id: 0,
					name: '',
					list: '',
					status: 'pause',
					accounts: '',
					leads: 0,
				}
				pattern.name = this.popupFileName;
				pattern.accounts = this.popupAccounts;
				this.$set(this.abmList, key, pattern);
				this.showHidePopups();
			}
		}
	}
</script>