<template lang="pug">
	div.card-box
		.card.nurturing(v-bind:class="{disable: !cardStatus}")
			CardHeader(
				:status="cardStatus",
				title="Nurturing",
				v-model="cardStatus"
				type="tooltip",
				:tooltipText="tooltipText"
			)

			.nurturing-table
				.t-head
					.cl
						p Type

					.cl
						p Assets Used

					.cl
						p Link

					.cl
						p Lead Goal
						
					.cl
						p Generated Leads

				.t-row(v-for="(item, index) in nurturingList", v-bind:class="{'active': item.status == 'play'}")
					.cl
						.action-play-pause(
							v-if="item.status",
							v-bind:class="{played: item.status == 'play'}"
