					DropBox(
						:list="item.answers",
						:default="item.selected",
						v-model="item.selected"
					)

		.popup.change-question(v-if="popupQuestion")
			.close(v-on:click="showHidePopups()")
			.inside-form
				label Question
				input(type="text", placeholder="Text here...", v-model="popupQuestionsForm.question", v-bind:class="").inp

				label Answer
				input(type="text", placeholder="Text here...", v-model="popupQuestionsForm.answer", v-bind:class="").inp

				button.btn.simple(type="button", v-on:click="saveQuestion()") SAVE CHANGES

</template>

<script>
	import CardHeader from "../components/title-checkbox.vue";
	import DropBox from "../components/drop-box.vue";

	export default {
		name: 'BantQustions',
		components:{
			CardHeader,
			DropBox
		},
		data() {
			return {
				cardStatus: true,
				questions: [
					{
						id: 0,
						title: 'Budget',
						question: 'Do you have a defined budget for this project?',
						selected: 'Limited - We need to review but are slightly low on available resources (Disqualify)',
						answers:{
							0: 'Limited - We need to review but are slightly low on available resources (Disqualify)',
							1: 'Satisfactory - We keep up with business demand but need to look at a way of scaling activity',
							2: 'Progressive - We are constantly having to develop to evolve with the times and now need to look at cost and time savings'
						}
					},
					{
						id: 1,
						title: 'Authority',
						question: 'Are you responsible for approving, authorizing or recommending this investment?',
						selected: 'Business Decision Maker',
						answers:{
							0: 'Business Decision Maker',
							1: 'Technical Decision Maker',
							2: 'Recommender Influencer',
							3: 'Evaluator',
							4: 'Information Gatherer',
							5: 'Other (Disqualify)'
						}
					},
					{
						id: 2,
						title: 'Need',
						question: 'Are you currently planning to evaluate or review an Inventory Management software solution?',
						selected: 'No',
						answers:{
							0: 'Yes',
							1: 'No'
						}
					},
					{
						id: 3,
						title: 'Time',
						question: 'What timeframe do you have to deploy desired solution?',
						selected: '7 - 9 months',
						answers:{
							0: '0 - 3 months',
							1: '3 - 6 months',
							2: '7 - 9 months',
							3: '10 - 12 months',
							4: 'More than 12 months (Disqualify)'
						}
					}
				],
				popupQuestion: false,
				popupQuestionsForm:{
					key: '',
					question: '',
					selected: '',
					answers: ''
				},
			}
		},
		created(){

		},
		methods: {
			editQuestion(index){
				var key = index;
				this.popupQuestionsForm = {
					key: key,
					question: this.questions[key].question,
					selected: this.questions[key].selected,
					answers: this.questions[key].answers
				}
				if (this.cardStatus) {
					this.popupQuestion = !this.popupQuestion;
					this.$emit('input', this.popupQuestion);
				}
			},
			showHidePopups(){
				this.newJobName = '';
				this.popupQuestion = false;
				this.$emit('input', false);
			},
			saveQuestion(){
				var newObj = {
					question: this.popupQuestionsForm.question,
					answers: this.popupQuestionsForm.answers,
					selected: this.popupQuestionsForm.selected
				}
				this.$set(this.questions, this.popupQuestionsForm.key, newObj);
				this.showHidePopups();
				this.popupQuestionsForm = {
					key: '',
					question: '',
					answer: ''
				}
			}
		}
	}
</script>