					.t-row.sum
						.cl
							p
								b Total

						.cl
							p
								b {{ total.lead_goal }}

						.cl
							p
								b {{ total.percent_completion }}%

						.cl
							p
								b {{ total.generated_leads }}

						.cl
							p
								b {{ total.remaining_leads }}

			.some-action
				p
					span Integration: 
				//- 	| Integrate
				DropList(:list="integration", default="Integrate")

				.action(v-on:click="showHidePopupIntegration()")

				p
					span Pacing:

				DropList(:list="pacing", :default="pacingVal", v-model="pacingVal")

			.chart-other
				.for-chart
					.chart
						pie-chart(:data="chartData", :options="chartOptions", ref="doughnutUpdate")

					.legend
						.inline
							.fill.blue
							p Delivered
							.line
							p.fix {{chartDataPercent[0]}}%

						.inline
							.fill.orange
							p In Process of Validation
							.line
							p.fix {{chartDataPercent[1]}}%

						.inline
							.fill.green
							p Remaining
							.line
							p.fix {{chartDataPercent[2]}}%

				.targeted
