			.stat
				.single
					span Input:
					p {{ total.generated_leads }}

				.single
					span Pending:
					p 82

				.single
					span Accepted:
					p.orange 637

				.single
					span Remaining:
					p.green 240

</template>

<script>
	import PieChart from "../components/PieChart.js";
	import DatePicker from 'vue2-datepicker';
	import 'vue2-datepicker/index.css';
	import DropList from "../components/drop-list.vue";
	import DropBox from "../components/drop-box.vue";
	import DropBoxInp from "../components/drop-box-inp.vue";

	export default {
		name: 'Campaign',
		components: {
			PieChart,
			DatePicker,
			DropList,
			DropBox,
			DropBoxInp
		},
		data() {
			return {
				editIndex: '',
				addType: false,
				startDate: new Date(new Date().setHours(0,0,0,0)),
				endDate: new Date(new Date(Date.now() + 1*24*60*60*1000).setHours(0,0,0,0)),
				campaignName:{
					0: '360° Finance Q2 BANT',
					1: '2020 Financial Vision',
					2: 'Nicolas Dunlop Finance'
				},
				campaignType:{
					0: 'Single Touch',
					1: 'Double Touch',
					2: 'Tripple Touch',
					3: 'BANT',
					4: 'Custom Questions',
					5: 'Impressions',
					6: 'Live Event',
					7: 'Live Webinar',
					8: 'BANT plus'
				},
				funnel:{
					0: 'Top',
					1: 'Middle',
					2: 'Bottom'
				},
				funnelDefault: 'Funnel',
				newCM: {
					type: '',
					lead_goal: '',
					priority: ''
				},
				newCMerrors:{
					type: false,
					lead_goal: false
				},
				customCM:{ },
				allCM:{
					0:{
						type: 'Single Touch',
						lead_goal: 810,
						percent_completion: 82,
						generated_leads: 664,
						remaining_leads: 146,
						priority: 'Top'
					},
					1:{
						type: 'BANT',
						lead_goal: 67,
						percent_completion: 82,
						generated_leads: 55,
						remaining_leads: 12,
						priority: 'Bottom'
					},
					// 2:{
					// 	type: 'Single Touch',
					// 	lead_goal: 100,
					// 	percent_completion: 82,
					// 	generated_leads: 70,
					// 	remaining_leads: 30,
					// 	priority: 'Top'
					// },
				},
				integration:{
					0: 'Salesforce',
					1: 'Marketo',
					2: 'Hubspot',
					3: 'Integrate',
					4: 'Lolagrove'
				},
				pacing:{
					0: 'Front-Load',
					1: 'Even'
				},
				pacingVal: 'Even',
				defaultPacing: '',
				total:{
					lead_goal: 0,
					percent_completion: 0,
					generated_leads: 0,
					remaining_leads: 0
				},
				boxesState:{
					ct: false,
					leads: false
				},
				chartOptions: {
					cutoutPercentage: 72,
					tooltips:{
						enabled: false
					}
				},
				chartData: {
					datasets: [
						{
							backgroundColor: ["#014165", "#FC9200", "#16AAC4"],
							data: [0, 0, 0],
							borderWidth: 0
						}
					]
				},
				chartDataPercent: [0, 0, 0],
				emptyTop: true,
				emptyMiddle: true,
				emptyBottom: true,
				limitExceeding: false,
				leadGoalError: '',
				popupIntegration: false
			}
		},
		watch: {
			startDate() {
				this.selectStartDate();
			},
			allCM: {
				handler: function() {
					if (this.leadGoalError == '') {
						this.calcTotal();
					}
				},
				deep: true
			},
			customCM: {
				handler: function() {
					if (this.leadGoalError == '') {
						this.calcTotal();
					}
				},
				deep: true
			},
			pacingVal: {
				handler: function() {
					if (this.leadGoalError == '') {
						this.calcTotal();
					}
				},
				deep: true
			}
		},
		created(){
			this.defaultPacing = this.pacingVal;
			this.calcTotal()
		},
		methods: {
			notBeforeToday(date) {
				return date < window.moment().add(-1, 'day');
			},
			selectStartDate(){
				if (this.startDate.getTime() >= this.endDate.getTime()) {
					this.endDate = new Date(window.moment(this.startDate).add(1, 'day'))
				}
			},
			addNewCM(){
				if (this.newCM.type.length <= 1) {
					this.newCMerrors.type = true;
				}
				if (this.newCM.lead_goal.length <= 1 && this.newCM.lead_goal > 0) {
					this.newCMerrors.lead_goal = true;
				}
				if (this.newCM.priority.length <= 1) {
					this.newCM.priority = 'error';
				}
				setTimeout(() => {
					this.newCMerrors.type = false;
					this.newCMerrors.lead_goal = false;
					this.newCM.priority = '';
				}, 1500)
				if (this.newCM.type.length > 1 && this.newCM.lead_goal.length > 1 && this.newCM.priority.length > 1) {
					var newCM = {
						type: this.newCM.type,
						lead_goal: this.newCM.lead_goal,
						percent_completion: '0%',
						generated_leads: 0,
						priority: this.newCM.priority
					}
					var key = '0_'+Object.keys(this.customCM).length;
					this.$set(this.customCM, key, newCM);
					this.newCM = {
						type: '',
						lead_goal: '',
						percent_completion: '',
						generated_leads: '',
						priority: ''
					}
					this.addType = false;
				}
			},
			editDataShow(index){
				if (this.leadGoalError == '') {
					if (this.editIndex == index) {
						this.editIndex = '';
					}else{
						this.editIndex = index;
						// const ref = ['goal_'+index];
						this.$refs.['goal_'+index][0].focus();
					}
				}
			},
			editLeadGoal(){
				if (this.leadGoalError == '') {
					this.editIndex = '';
				}
			},
			checkLeadsInp(index, obj){
				var num = this.allCM[index].lead_goal;
				if (isNaN(num)) {
					this.allCM[index].lead_goal = 1
				}
				if (obj == 'all') {
					if (num < this.allCM[index].generated_leads || num < 1) {
						this.leadGoalError = index;
					}else{
						this.leadGoalError = '';
					}
				}
			},
			updateDoughnut() {
				this.$refs.doughnutUpdate.update();
			},
			addNewType(type){
				var count =  Object.keys(this.allCM).length + Object.keys(this.customCM).length;
				if(count == 10){
					this.limitExceeding = true;
					setTimeout(() => {
						this.limitExceeding = false;
					}, 1500)
				}else{
					if (type) {
						this.addType = true;
						this.newCM.priority = type;
						this.funnelDefault = type;
					}else{
						this.addType = !this.addType;
						this.newCM.priority = '';
						this.funnelDefault = 'Funnel';
					}
				}
			},
			calcTotal(){
				this.total = {
					lead_goal: 0,
					percent_completion: 0,
					generated_leads: 0,
					remaining_leads: 0
				}
				var allLength = Object.keys(this.allCM).length;
				for (var itemCM in this.allCM) {
					this.total.lead_goal = this.total.lead_goal + parseInt(this.allCM[itemCM].lead_goal);
					this.allCM[itemCM].percent_completion = Math.round(parseInt(this.allCM[itemCM].generated_leads) /  parseInt(this.allCM[itemCM].lead_goal) * 100)
					this.total.percent_completion = this.total.percent_completion + parseInt(this.allCM[itemCM].percent_completion);
					this.total.generated_leads = this.total.generated_leads + parseInt(this.allCM[itemCM].generated_leads);
					this.allCM[itemCM].remaining_leads = parseInt(this.allCM[itemCM].lead_goal) - parseInt(this.allCM[itemCM].generated_leads);
					this.total.remaining_leads = this.total.remaining_leads + parseInt(this.allCM[itemCM].remaining_leads);
					if(this.allCM[itemCM].priority == 'Top'){
						this.emptyTop = false;
					}
					if(this.allCM[itemCM].priority == 'Middle'){
						this.emptyMiddle = false;
					}
					if(this.allCM[itemCM].priority == 'Bottom'){
						this.emptyBottom = false;
					}
				}

				var customLength = Object.keys(this.customCM).length;
				for (var item in this.customCM) {
					this.total.lead_goal = this.total.lead_goal + parseInt(this.customCM[item].lead_goal);
					this.customCM[item].percent_completion = Math.round(parseInt(this.customCM[item].generated_leads) / parseInt(this.customCM[item].lead_goal) * 100)
					this.total.percent_completion = this.total.percent_completion + parseInt(this.customCM[item].percent_completion);
					this.total.generated_leads = this.total.generated_leads + parseInt(this.customCM[item].generated_leads);
					this.customCM[item].remaining_leads = parseInt(this.customCM[item].lead_goal) - parseInt(this.customCM[item].generated_leads);
					this.total.remaining_leads = this.total.remaining_leads + parseInt(this.customCM[item].remaining_leads);
					if(this.customCM[item].priority == 'Top'){
						this.emptyTop = false;
					}
					if(this.customCM[item].priority == 'Middle'){
						this.emptyMiddle = false;
					}
					if(this.customCM[item].priority == 'Bottom'){
						this.emptyBottom = false;
					}
				}
				this.total.percent_completion = Math.round(this.total.percent_completion / (allLength + customLength))

				var Delivered = Math.round(this.total.generated_leads / this.total.lead_goal * 100);
				var sum = Math.round(this.total.remaining_leads / this.total.lead_goal * 100)
				var percent;
				if (this.defaultPacing == 'Even') {
					percent = 30;
				}else{
					percent = 60;
				}
				var UnderVerification = Math.round(sum * percent / 100);
				var Remaining = Math.round(sum - UnderVerification);

				this.chartDataPercent = [Delivered, UnderVerification, Remaining];
				this.chartData.datasets[0].data = this.chartDataPercent;
				this.$nextTick(function () {
					this.updateDoughnut();
				});
			},
			showHidePopupIntegration(){
				this.popupIntegration = !this.popupIntegration;
				this.$emit('input', this.popupIntegration);
			}
		}
	}
</script>