export default{
	init(){
		window.store.commit("selectCampaign");
	},
	setData(data){
		localStorage.setItem('selectCampaignID', data.id);
		localStorage.setItem('selectCampaignName', data.name);
		localStorage.setItem('campaignClient', data.clientName);
		this.init();
	},
	deleteData(){
		localStorage.removeItem('selectCampaignID');
		localStorage.removeItem('selectCampaignName');
		localStorage.removeItem('campaignClient');
		this.init();
	}
}