								rect(x="11" y="5" width="2" height="10" rx="1").f-t

					.question-settings(v-on:click="editQuestion(index)")
						p {{ item.question }}
						.settings
							svg(width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg")
								path(d="M9.96641 5.56628C9.98696 5.3804 10 5.19191 10 5.00016C10 4.80841 9.98696 4.61993 9.96609 4.43372L8.53845 3.92597C8.46736 3.69248 8.37409 3.46943 8.26127 3.25746L8.91085 1.89043C8.67444 1.59395 8.40558 1.32487 8.10931 1.08821L6.74232 1.73912C6.52925 1.62474 6.30531 1.53189 6.07383 1.46193L5.56643 0.034241C5.38023 0.0133703 5.19174 0 5 0C4.80826 0 4.61977 0.0130442 4.43357 0.034241L3.92617 1.46193C3.69269 1.53269 3.46964 1.62563 3.25768 1.73912L1.89037 1.08821C1.59362 1.32464 1.32492 1.59367 1.08818 1.89043L1.73906 3.25681C1.62558 3.46878 1.53264 3.69216 1.46188 3.92532L0.0342399 4.43405C0.0133699 4.61993 0 4.80907 0 5.00049C0 5.19191 0.0130438 5.3804 0.0342399 5.56661L1.46188 6.07468C1.53264 6.30784 1.62558 6.53122 1.73906 6.74319L1.0885 8.11022C1.32492 8.40698 1.59395 8.67536 1.89069 8.91179L3.2567 8.26121C3.46866 8.37469 3.69204 8.46796 3.92519 8.53905L4.43325 9.96641C4.61912 9.98696 4.8076 10 4.99967 10C5.19142 10 5.37957 9.98696 5.56577 9.96609L6.07383 8.5384C6.30698 8.46731 6.53036 8.37404 6.74232 8.26121L8.10931 8.91081C8.40605 8.67439 8.67443 8.40535 8.91085 8.10925L8.26094 6.74254C8.37442 6.53155 8.46768 6.30752 8.53877 6.07403L9.96641 5.56628ZM5 6.58829C4.79141 6.58834 4.58485 6.54729 4.39212 6.4675C4.19939 6.38772 4.02426 6.27075 3.87673 6.12328C3.57879 5.82544 3.41136 5.42145 3.41127 5.00016C3.41118 4.57888 3.57845 4.17481 3.87627 3.87686C4.17409 3.57891 4.57808 3.41147 4.99935 3.41138C5.42062 3.41129 5.82467 3.57857 6.12261 3.8764C6.42056 4.17423 6.58799 4.57823 6.58808 4.99951C6.58816 5.4208 6.4209 5.82486 6.12308 6.12281C5.82525 6.42077 5.42127 6.58821 5 6.58829Z" fill="#4F4F4F")


					DropBox(
						:list="item.answers",
						:default="item.selected",
						:v-model="item.selected"
					)

		.popup.change-question(v-if="popupQuestion")
			.close(v-on:click="showHidePopups()")
			.inside-form
				label Question
				input(type="text", placeholder="Text here...", v-model="popupQuestionsForm.question", v-bind:class="").inp

				label Answer
				input(type="text", placeholder="Text here...", v-model="popupQuestionsForm.answer", v-bind:class="").inp

				button.btn.simple(type="button", v-on:click="saveQuestion()") SAVE CHANGES

</template>

<script>
	import CardHeader from "../components/title-checkbox.vue";
	import DropBox from "../components/drop-box.vue";

	export default {
		name: 'CustomQuestions',
		components:{
			CardHeader,
			DropBox
		},
		data() {
			return {
				cardStatus: true,
				questions: [
					{
						id: 0,
						status: 'play',
						question: '1. How familiar are you with Splunk?',
						selected: 'Functionality gaps 115',
						answers:{
							0: 'Functionality gaps 115',
							1: 'Struggle keeping the core clean with technical debt 202',
							2: 'Team lacks deep experience in modern cloud-based architecture 89',
							3: 'Slow delivery on new applications 216',
							4: 'Other (Please specify) 412'
						}
					},
					{
						id: 1,
						status: 'paused',
						question: '2. Have you seen a Demo before?',
						selected: 'Answer 3',
						answers:{
							0: 'Answer 1',
							1: 'Answer 2',
							2: 'Answer 3'
						}
					},
					{
						id: 2,
						status: 'paused',
						question: '3. Have you downloaded it?',
						selected: 'Answer 2',
						answers:{
							0: 'Answer 1',
							1: 'Answer 2',
							2: 'Answer 3'
						}
					},
					{
						id: 3,
						status: 'play',
						question: '4. Tell me about the last outage that affected a key Line of Business.',
						selected: 'Limited - We need to review but are slightly low on available resources (Disqualify) 48',
						answers:{
							0: 'Limited - We need to review but are slightly low on available resources (Disqualify) 48',
							1: 'Satisfactory - We keep up with business demand but need to look at a way of scaling activity 64',
							2: 'Progressive - We are constantly having to develop to evolve with the times and now need to look at cost and time savings 401'
						}
					}
				],
				popupQuestion: false,
				popupQuestionsForm:{
					key: '',
					question: '',
					selected: '',
					answers: ''
				},
			}
		},
		created(){

		},
		methods: {
			changeCQStatus(index){
				if (this.cardStatus) {
					if(this.questions.[index].status == 'play'){
						this.questions.[index].status = 'paused'
					}else{
						this.questions.[index].status = 'play'
					}
				}
			},
			editQuestion(index){
				var key = index;
				this.popupQuestionsForm = {
					key: key,
					question: this.questions[key].question,
					selected: this.questions[key].selected,
					answers: this.questions[key].answers
				}
				if (this.cardStatus) {
					this.popupQuestion = !this.popupQuestion;
					this.$emit('input', this.popupQuestion);
				}
			},
			showHidePopups(){
				this.newJobName = '';
				this.popupQuestion = false;
				this.$emit('input', false);
			},
			saveQuestion(){
				var newObj = {
					question: this.popupQuestionsForm.question,
					answers: this.popupQuestionsForm.answers,
					selected: this.popupQuestionsForm.selected
				}
				this.$set(this.questions, this.popupQuestionsForm.key, newObj);
				this.showHidePopups();
				this.popupQuestionsForm = {
					key: '',
					question: '',
					answer: ''
				}
			}
		}
	}
</script>