<template lang="pug">
	div.card-box
		.card.industries(v-bind:class="{disable: !cardStatus}")
			CardHeader(:status="cardStatus", title="Industries", v-model="cardStatus")

			.industries-table
				.t-head
					.cl
						p Industry

					.cl
						p Leads

					.cl
						.chart-range(ref="chart")
							.single
								p 0

							.single
								p {{ Number(maxNumber / maxDivide * 1).toFixed(0) }}

							.single
								p {{ Number(maxNumber / maxDivide * 2).toFixed(0) }}

							.single
								p {{ Number(maxNumber / maxDivide * 3).toFixed(0) }}

				.t-row(v-for="(item, index) in industriesList", v-bind:class="{'active': item.status == 'play'}")
					.cl
						.action-play-pause(
							v-if="item.status",
							v-bind:class="{played: item.status == 'play'}"
							v-on:click="changeIndustriesStatus(index)"
						)
							.play
								svg(width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg")
									rect(x="0.5" y="0.5" width="19" height="19" rx="2.5").stroke
									path(d="M7.23822 5.057C7.30311 5.019 7.37378 5 7.44444 5C7.53333 5 7.62133 5.03 7.69733 5.089L13.4751 9.5891C13.5951 9.6826 13.6667 9.83611 13.6667 10.0001C13.6667 10.1641 13.5951 10.3176 13.4751 10.4111L7.69733 14.9112C7.56178 15.0167 7.38444 15.0292 7.23822 14.9432C7.09156 14.8567 7 14.6862 7 14.5002V5.50001C7 5.31401 7.09156 5.1435 7.23822 5.057Z").fill
							.pause
								svg(width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg")
									rect(width="20" height="20" rx="3").f-f
									rect(x="7" y="5" width="2" height="10" rx="1").f-t
									rect(x="11" y="5" width="2" height="10" rx="1").f-t

						p {{ item.industry }}

					.cl
						p(v-if="item.percents") {{ Number(generatedLeads * (item.percents / 100)).toFixed(0) }}
						p(v-else) -

					.cl
						.fill-line
							.lines
								.s-line
								.s-line
								.s-line
								.s-line

							.fill(:style='{width: Number(generatedLeads * (item.percents / 100)).toFixed(0) / maxNumber * 100 + "%"}')
								p(
									v-bind:class="{'push': chartWidth * Number(Number(generatedLeads * (item.percents / 100)).toFixed(0) / maxNumber * 100).toFixed(2) / 100 < 40 }"
							) {{ item.percents }}%

			button.btn.add-new-btn(type="button", v-on:click="addIndustries()") ADD A NEW INDUSTRY

		.popup.add-industries-p(v-if="popupAddIndustries")
			.close(v-on:click="showHidePopups()")
			.inside-form
				label Industries Title
				input(type="text", placeholder="Text here...", v-model="newIndustriesName", v-bind:class="{error: newIndustriesNameError}").inp

				button.btn.simple(type="button", v-on:click="saveIndustries()") SUBMIT

</template>

<script>
	import vuescroll from 'vuescroll'
	import CardHeader from "../components/title-checkbox.vue";
	import DropList from "../components/drop-list.vue";

	export default {
		name: 'Industries',
		components:{
			CardHeader,
			DropList,
			vuescroll
		},
		data() {
			return {
				generatedLeads: 719,
				cardStatus: true,
				industriesList:[
					{
						id: 0,
						industry: 'Manufacturing',
						leads: '',
						percents: '42.57',
						status: 'play'
					},
					{
						id: 1,
						industry: 'Finance & Banking ',
						leads: '',
						percents: '34.39',
						status: 'pause'
					},
					{
						id: 2,
						industry: 'Technology',
						leads: '',
						percents: '33.16',
						status: 'play'
					},
					{
						id: 3,
						industry: 'Government',
						leads: '',
						percents: '22.2',
						status: 'pause'
					},
					{
						id: 4,
						industry: 'Professional Services',
						leads: '',
						percents: '18.3',
						status: 'play'
					},
					{
						id: 5,
						industry: 'Healthcare & Pharma',
						leads: '',
						percents: '18.3',
						status: 'play'
					},
					{
						id: 6,
						industry: 'Retail',
						leads: '',
						percents: '12.5',
						status: 'play'
					},
					{
						id: 7,
						industry: 'Insurance',
						leads: '',
						percents: '7.3',
						status: 'play'
					}
				],
				chartWidth: '',
				maxDivide: 3,
				maxNumber: 100,
				emptyGoal: true,
				ops:{
					bar:{
						background: '#FC9000',
						onlyShowBarOnScroll: false,
						keepShow: true,
						size: '2px'
					},
					rail:{
						background: '#E6E6E6',
						opacity: 1,
						size: '2px'
					}
				},
				newIndustriesName: '',
				popupAddIndustries: false,
				newIndustriesNameError: false
			}
		},
		created(){
			this.setMaxInt();
		},
		mounted() {
			// window.addEventListener('resize', this.handleResize);
			this.handleResize();
		},
		methods: {
			handleResize() {
				this.chartWidth = this.$refs.chart.clientWidth;
			},
			setMaxInt(){
				var max = this.maxNumber;
				for (var item in this.industriesList) {
					var leads = Number(this.generatedLeads * (this.industriesList[item].percents / 100)).toFixed(0);
					var num = parseInt(leads);
					if (max < num && !isNaN(num)) {
						max = num
					}
				}

				var r = parseInt(max.toString()[0]);
				check();
				var x;
				function check(){
					x = r * 100;
					if(x < max){
						r = r + 1;
						check();
					}
				}
				this.maxNumber = x;
			},
			changeIndustriesStatus(index){
				if (this.cardStatus) {
					if(this.industriesList.[index].status == 'play'){
						this.industriesList.[index].status = 'pause'
					}else{
						this.industriesList.[index].status = 'play'
					}
				}
			},
			addIndustries(){
				if (this.cardStatus) {
					this.popupAddIndustries = !this.popupAddIndustries;
					this.$emit('input', this.popupAddIndustries);
				}
			},
			showHidePopups(){
				this.newIndustriesName = '';
				this.popupAddIndustries = false;
				this.$emit('input', false);
			},
			saveIndustries(){
				if (this.newIndustriesName.length < 2){
					this.newIndustriesNameError = true;
					setTimeout(() => {
						this.newIndustriesNameError = false;
					}, 1500)
				}else{
					var key =  Object.keys(this.industriesList).length;
					var pattern = {
						id: key,
						industry: '',
						leads: 0,
						status: 'pause'
					}
					pattern.industry = this.newIndustriesName;
					this.$set(this.industriesList, key, pattern);
					this.showHidePopups();
				}
			}
		}
	}
</script>