<template lang="pug">
	div.card-box
		.card.geographical(v-bind:class="{disable: !cardStatus}")
			CardHeader(:status="cardStatus", title="Geographical Locations", v-model="cardStatus")
			.flex-row
				.left-side
					.geographical-table
						.t-head
							.cl

							.cl
								p Geolocation

							.cl
								p Leads

							.cl
								p(v-bind:class="{empty: emptyGoal}") Goal per Geo

						.t-row(v-for="(item, index) in geolocationList", v-bind:class="{'active': item.status == 'play'}")
							.cl
								.action-play-pause(
									v-if="item.status",
									v-bind:class="{played: item.status == 'play'}"
									v-on:click="changeGeoStatus(index)"
								)
									.play
										svg(width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg")
											rect(x="0.5" y="0.5" width="19" height="19" rx="2.5").stroke
											path(d="M7.23822 5.057C7.30311 5.019 7.37378 5 7.44444 5C7.53333 5 7.62133 5.03 7.69733 5.089L13.4751 9.5891C13.5951 9.6826 13.6667 9.83611 13.6667 10.0001C13.6667 10.1641 13.5951 10.3176 13.4751 10.4111L7.69733 14.9112C7.56178 15.0167 7.38444 15.0292 7.23822 14.9432C7.09156 14.8567 7 14.6862 7 14.5002V5.50001C7 5.31401 7.09156 5.1435 7.23822 5.057Z").fill
									.pause
										svg(width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg")
											rect(width="20" height="20" rx="3").f-f
											rect(x="7" y="5" width="2" height="10" rx="1").f-t
											rect(x="11" y="5" width="2" height="10" rx="1").f-t

							.cl
								p {{ item.geolocation }}

							.cl
								p(v-if="item.percents") {{ Number(generatedLeads * (item.percents / 100)).toFixed(0) }}
								p(v-else) -

							.cl
								p(v-if="item.gpg") {{ item.gpg }}
								p(v-else) -
					
					button.btn.add-new-btn(type="button", v-on:click="addGeo()") ADD A NEW GEOLOCATION

				.right-side
					.map


		.popup.add-industries-p(v-if="popupAddGeo")
			.close(v-on:click="showHidePopups()")
			.inside-form
				label Geographical Locations
				input(type="text", placeholder="Text here...", v-model="newGeoName", v-bind:class="{error: newGeoNameError}").inp

				button.btn.simple(type="button", v-on:click="saveGeo()") SUBMIT
</template>

<script>
	import CardHeader from "../components/title-checkbox.vue";
	import DropList from "../components/drop-list.vue";

	export default {
		name: 'GeographicalLocations',
		components:{
			CardHeader,
			DropList
		},
		data() {
			return {
				generatedLeads: 719,
				cardStatus: true,
				geolocationList: [
					{
						id: 0,
						geolocation: 'The United Kingdom',
						leads: '',
						percents: '20',
						gpg: '20% (512)',
						status: 'play'
					},
					{
						id: 1,
						geolocation: 'Russia',
						leads: '',
						percents: '10',
						gpg: '',
						status: 'pause'
					},
					{
						id: 2,
						geolocation: 'France',
						leads: '',
						percents: '12',
						gpg: '',
						status: 'play'
					},
					{
						id: 3,
						geolocation: 'Spain',
						leads: '',
						percents: '11',
						gpg: '',
						status: 'pause'
					},
					{
						id: 4,
						geolocation: 'The Netherlands',
						leads: '',
						percents: '5',
						gpg: '',
						status: 'pause'
					},
					{
						id: 5,
						geolocation: 'Germany',
						leads: '',
						percents: '76',
						gpg: '',
						status: 'pause'
					},
					{
						id: 6,
						geolocation: 'Poland',
						leads: '',
						percents: '99',
						gpg: '',
						status: 'pause'
					},
					{
						id: 7,
						geolocation: 'Czech Republic',
						leads: '',
						percents: '71',
						gpg: '',
						status: 'pause'
					}
				],
				emptyGoal: true,
				newGeoName: '',
				popupAddGeo: false,
				newGeoNameError: false
			}
		},
		created(){
			this.checkList();
		},
		methods: {
			changeGeoStatus(index){
				if (this.cardStatus) {
					if(this.geolocationList.[index].status == 'play'){
						this.geolocationList.[index].status = 'pause'
					}else{
						this.geolocationList.[index].status = 'play'
					}
				}
			},
			checkList(){
				for (var item in this.geolocationList) {
					var gpg = this.geolocationList[item].gpg;
					if (gpg.length > 1 || gpg > 0) {
						this.emptyGoal = false;
						return;
					}
				}
			},
			addGeo(){
				if (this.cardStatus) {
					this.popupAddGeo = !this.popupAddGeo;
					this.$emit('input', this.popupAddGeo);
				}
			},
			showHidePopups(){
				this.newGeoName = '';
				this.popupAddGeo = false;
				this.$emit('input', false);
			},
			saveGeo(){
				if (this.newGeoName.length < 2){
					this.newGeoNameError = true;
					setTimeout(() => {
						this.newGeoNameError = false;
					}, 1500)
				}else{
					var key =  Object.keys(this.geolocationList).length;
					var pattern = {
						id: key,
						geolocation: '',
						leads: 0,
						gpg: '',
						status: 'pause'
					}
					pattern.geolocation = this.newGeoName;
					this.$set(this.geolocationList, key, pattern);
					this.showHidePopups();
				}
			}
		}
	}
</script>