<template lang="pug">
	div.card-box
		.card.company-size(v-bind:class="{disable: !cardStatus}")
			CardHeader(:status="cardStatus", title="Company Size", v-model="cardStatus")
			.flex-row
				.left-side
					//- h6 The company size range
					.list
						.single(v-for="(item, index) in companyRangeList", v-bind:class="{'active': item.status == 'play'}")
							.action-play-pause(
								v-if="item.status",
								v-bind:class="{played: item.status == 'play'}"
								v-on:click="changeCompanyRangeStatus(index)"
							)
								.play
									svg(width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg")
										rect(x="0.5" y="0.5" width="19" height="19" rx="2.5").stroke
										path(d="M7.23822 5.057C7.30311 5.019 7.37378 5 7.44444 5C7.53333 5 7.62133 5.03 7.69733 5.089L13.4751 9.5891C13.5951 9.6826 13.6667 9.83611 13.6667 10.0001C13.6667 10.1641 13.5951 10.3176 13.4751 10.4111L7.69733 14.9112C7.56178 15.0167 7.38444 15.0292 7.23822 14.9432C7.09156 14.8567 7 14.6862 7 14.5002V5.50001C7 5.31401 7.09156 5.1435 7.23822 5.057Z").fill
								.pause
									svg(width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg")
										rect(width="20" height="20" rx="3").f-f
										rect(x="7" y="5" width="2" height="10" rx="1").f-t
										rect(x="11" y="5" width="2" height="10" rx="1").f-t

							p {{ item.name }}

				.right-side
					h6 Company Size Captured

					.chart-block
						.legend
							.inline
								.fill
								p 1,000 & Above
								.line
								p.fix 1,231

							.inline
								.fill
								p 501 - 1,000
								.line
								p.fix 514

							.inline
								.fill
								p 5,001 - 10,000
								.line
								p.fix 448

							.inline
								.fill
								p 1,001 - 5,000
								.line
								p.fix 318
					
						.chart
							pie-chart(:data="chartData", :options="chartOptions", ref="doughnutUpdate")

			button.btn.add-new-btn(type="button", v-on:click="addCompanyRange()") ADD RANGE

		.popup.add-industries-p(v-if="popupAddCompanyRange")
			.close(v-on:click="showHidePopups()")
			.inside-form
				label Company Size
				input(type="text", placeholder="For Example: 700-1200", v-model="newCompanyRangeName", v-bind:class="{error: newCompanyRangeNameError}").inp

				button.btn.simple(type="button", v-on:click="saveCompanyRange()") SUBMIT

</template>

<script>
	import PieChart from "../components/PieChart.js";
	import CardHeader from "../components/title-checkbox.vue";
	import DropList from "../components/drop-list.vue";

	export default {
		name: 'CompanySize',
		components:{
			PieChart,
			CardHeader,
			DropList
		},
		data() {
			return {
				cardStatus: true,
				companyRangeList: [
					{
						id: 0,
						name: 'Under 10 employees',
						status: 'pause'
					},
					{
						id: 1,
						name: '11 to 50',
						status: 'pause'
					},
					{
						id: 2,
						name: '51 to 200',
						status: 'pause'
					},
					{
						id: 3,
						name: '201 to 500',
						status: 'pause'
					},
					{
						id: 4,
						name: '501-1,000 (514)',
						status: 'play'
					},
					{
						id: 5,
						name: '1,001-5,000 (318)',
						status: 'play'
					},
					{
						id: 6,
						name: '5,001-10,000 (448)',
						status: 'play'
					},
					{
						id: 7,
						name: '10,000 & Above (1,231)',
						status: 'play'
					},
				],
				boxesState:{
					ct: false,
					leads: false
				},
				chartOptions: {
					cutoutPercentage: 72,
					// tooltips:{
					// 	enabled: false
					// },
					legend:{
						display: false
					},
				},
				chartData: {
					labels: ['1,000 & Above', '501 - 1,000', '5,001 - 10,000', '1,001 - 5,000'],
					datasets: [
						{
							backgroundColor: ["#014165", "#FFC000", "#16AAC4", "#FC9200", "#FF5722", "#69D09F", "#00C2FF", "#2A76BC"],
							data: [5, 40, 30, 25],
							borderWidth: 0
						}
					]
				},
				chartDataPercent: [5, 40, 30, 25],
				newCompanyRangeName: '',
				popupAddCompanyRange: false,
				newCompanyRangeNameError: false
			}
		},
		created(){
			this.$nextTick(function () {
				this.updateDoughnut();
			});
		},
		watch:{
			cardStatus: function(){
				this.updateDoughnut();
			}
		},
		methods: {
			updateDoughnut() {
				if (this.cardStatus) {
					this.chartData.datasets[0].backgroundColor = ["#014165", "#FFC000", "#16AAC4", "#FC9200", "#FF5722", "#69D09F", "#00C2FF", "#2A76BC"];
				}else{
					this.chartData.datasets[0].backgroundColor = ["#053754", "#E9B20B", "#0694AD", "#F07B07", "#E14413", "#3FAB77", "#0EA5D5", "#125DA2"];
				}
				this.$refs.doughnutUpdate.update();
			},
			changeCompanyRangeStatus(index){
				if (this.cardStatus) {
					if(this.companyRangeList.[index].status == 'play'){
						this.companyRangeList.[index].status = 'pause'
					}else{
						this.companyRangeList.[index].status = 'play'
					}
				}
			},
			addCompanyRange(){
				if (this.cardStatus) {
					this.popupAddCompanyRange = !this.popupAddCompanyRange;
					this.$emit('input', this.popupAddCompanyRange);
				}
			},
			showHidePopups(){
				this.newCompanyRangeName = '';
				this.popupAddCompanyRange = false;
				this.$emit('input', false);
			},
			saveCompanyRange(){
				if (this.newCompanyRangeName.length < 2){
					this.newCompanyRangeName = true;
					setTimeout(() => {
						this.newCompanyRangeNameError = false;
					}, 1500)
				}else{
					var key =  Object.keys(this.companyRangeList).length;
					var pattern = {
						id: key,
						name: '',
						status: 'pause'
					}
					pattern.name = this.newCompanyRangeName;
					this.$set(this.companyRangeList, key, pattern);
					this.showHidePopups();
				}
			}
		}
	}
</script>